.trending {
  display: flex;
  align-items: center;

  &__title {
    margin-bottom: 0;
    text-align: right;
    padding: 0 1.75rem;

    &--decorated {
      color: $primary;
    }
  }

  &__list {
    display: flex;
    margin: 0;
  }

  &__list-item {
    padding: 0 1.75rem;
    border-left: 1px solid $white;
  }
}