.carousel {
  padding: 2rem 0;

  &__wrapper {
    display: flex;
    align-items: center;
    position: relative;
  }

  &__arrow {
    display: none;
    position: relative;
    z-index: 1;
    min-width: 1rem;
    width: 1rem;
    padding: 0;
    border: 0;
    background: transparent;
    transition: transform .15s ease-in-out;

    @include media-breakpoint-up(md) {
      display: block;
    }

    &:hover,
    &:focus {
      transform: scale(1.125);
      outline: 0;
      box-shadow: none;
      border-color: transparent;
    }
  }

  &__arrow-img {
    width: 100%;
  }

  &__previous {
    left: -1rem;

    &:active {
      transform: translateX(-0.125rem) scale(1.125);
    }
  }

  &__next {
    right: -1rem;

    &:active {
      transform: translateX(0.125rem) scale(1.125);
    }
  }

  &__hidden {
    display: none;

    @include media-breakpoint-up(md) {
      display: block
    }
  }

  &__bullets {
    display: flex;
    justify-content: center;
    margin-top: 1.5rem;

    @include media-breakpoint-up(md) {
      display: none;
    }
  }

  &__bullet {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1.25rem;
    height: 1.25rem;
    margin: 0 0.5rem;
    padding: 0;
    border: 0 none;
    border-radius: 1.25rem;
    background-color: $gray-500;
    transition: background-color .15s ease-in-out;

    &:hover,
    &:active {
      background-color: $gray-700;
    }

    &:focus,
    &--active {
      background-color: $primary;
      outline: 0;
      box-shadow: none;
      border-color: transparent;
    }
  }
}