.cta {
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 0;
  margin: 0;

  &__title {
    margin-bottom: 1rem;
    font-size: 3rem;
    text-transform: uppercase;

    @include media-breakpoint-up(sm) {
      font-size: 6rem;
    }

    &::after {
      content: "";
      display: inline-block;
      width: 1rem;
      height: 1rem;
      margin-left: 0.5rem;
      border-radius: 1rem;
      background-color: $primary;
    }
  }

  &__text {
    font-size: 1.125rem;
  }

  &__button {
    margin-top: .5rem;
    font-size: 1.125rem;

    @include media-breakpoint-up(sm) {
      margin-top: 1.5rem;
    }
  }
}