.cover {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 100%;
  background-color: rgba($black, 0.5);
  color: $white;

  &__img {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: url('./images/space.jpeg') center center no-repeat fixed $primary;
    background-size: cover;
    transform: scaleX(-1);
  }

  &__main {
    margin-top: auto;
    margin-bottom: auto;

    @include media-breakpoint-up(lg) {
      margin-bottom: 0;
    }
  }

  &__bottom {
    display: none;
    margin-top: auto;
    padding: 1rem;
    background-color: rgba($black, 0.5);

    @include media-breakpoint-up(lg) {
      display: block;
    }
  }
}
