.navigation {
  position: relative;
  z-index: 1;
  height: 7rem;
  margin-bottom: -7rem;
  padding: 1rem 0;
  transition: background-color .15s ease-in-out, transform .15s ease-in-out;

  @include media-breakpoint-up(sm) {
    padding-top: 2rem;
    height: 8rem;
    margin-bottom: -8rem;
  }

  &--fixed {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    background-color: $primary;

    @include media-breakpoint-up(sm) {
      transform: translateY(-1rem);
    }
  }

  &__bar {
    display: flex;
    flex-wrap: nowrap;
  }

  &__logo {
    position: relative;
    z-index: 3;
    text-align: left;
    padding: 0;
    border: 0;
    background: transparent;
    line-height: $headings-line-height;
    font-weight: bold;
    color: $white;
    transition: transform .15s ease-in-out;

    &:hover,
    &:active {
      transform: scale(0.925);
    }

    &:focus {
      transform: scale(1.125);
      outline: 0;
      box-shadow: none;
      border-color: transparent;
    }
  }

  &__logo-img {
    height: 5rem;
  }

  &__main {
    display: flex;
    justify-content: flex-end;
    width: 100%;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: unset;
    justify-content: unset;
    position: fixed;
    z-index: 2;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 7rem 1rem 1rem;
    background-color: $primary;
    text-align: center;
    font-size: 1.25rem;
    transform: translateX(100%);
    transition: transform .15s cubic-bezier(1, 0, 0, 1);

    @include media-breakpoint-up(sm) {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      position: relative;
      z-index: 2;
      width: 75%;
      padding: 0;
      background-color: transparent;
      font-size: 1rem;
      margin: 0;
      transform: translateX(0);
    }

    @include media-breakpoint-up(lg) {
      width: 50%;
    }

    &--overlay {
      transform: translateX(0);
    }
  }

  &__list-item {
    padding: 1rem 0;
  }

  &__link {
    color: $white;
    text-transform: uppercase;
    transition: transform .15s ease-in-out;

    &:hover,
    &:active {
      color: $white;
      text-decoration: none;
      transform: scale(1.125);
    }

    &:focus {
      transform: scale(1.125);
      font-weight: bold;
      outline: 0;
      box-shadow: none;
      border-color: transparent;
    }
  }

  &__menu {
    position: relative;
    z-index: 1;
    text-align: right;
    width: 2.5rem;
    padding: 0;
    border: 0;
    background: transparent;
    color: $white;
    transition: transform .15s ease-in-out;

    @include media-breakpoint-up(sm) {
      display: none;
    }

    &:hover,
    &:active {
      transform: scale(0.925);
    }

    &:focus {
      transform: scale(1.125);
      outline: 0;
      box-shadow: none;
      border-color: transparent;
    }
  }

  &__menu-img {
    width: 100%;
  }

  &__close {
    display: none;
    position: relative;
    z-index: 3;
    text-align: right;
    width: 2rem;
    margin-left: -2rem;
    padding: 0;
    border: 0;
    background: transparent;
    color: $white;
    transition: transform .15s ease-in-out;

    &--active {
      display: block;

      @include media-breakpoint-up(sm) {
        display: none;
      }
    }

    &:hover,
    &:active {
      transform: scale(0.925);
    }

    &:focus {
      transform: scale(1.125);
      outline: 0;
      box-shadow: none;
      border-color: transparent;
    }
  }

  &__close-img {
    width: 100%;
  }
}