.about {
  padding: 2rem 0 1rem;

  &__img-wrapper {
    border-radius: .5rem;

    @include media-breakpoint-up(lg) {
      display: flex;
      justify-content: center;
      position: relative;
      overflow: hidden;
      height: 100%;
    }
  }

  &__img {
    border-radius: .5rem;

    @include media-breakpoint-up(lg) {
      position: absolute;
      height: 100%;
      max-width: unset;
    }
  }

  &__title {
    margin: 2rem 0 1.75rem;
    text-transform: uppercase;
    color: $primary;

    @include media-breakpoint-up(lg) {
      margin-top: 1rem;
    }
  }
}