html, body, #root, .wrapper, .main {
  height: 100%;
  min-height: 30rem;

  @include media-breakpoint-up(sm) {
    min-height: 35rem;
  }
}

body {
  background-color: unset;
}

.content {
  background-color: $gray-100;

  @include media-breakpoint-up(md) {
    padding-top: 3rem;
  }
}

.footer {
  padding: 2rem 0 1rem;
  background-color: $gray-100;
}